
.editable-cmp {
    border: 1px solid transparent;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    &:hover {
        border: 1px dashed black;
    }
    &:focus {
        outline: 1.5px dashed black;
    }
    padding: 0 5px;
    width  : fit-content;
    height: fit-content;
}